import React from 'react';

export default class Notification extends React.Component {
	constructor(props) {
    super(props);
    var notification = "";
    var commentCount = 0;
    var refreshInterval = 5000;
    var interval = 1;
		
	}
	
	setNotification  = (count) =>{
		if(count>0){
			this.commentCount = count;
		}
		this.setState({"abc" : ""});
	} 
	
	componentDidMount() {
		var that = this;
		var checkNewComment = require('./CommentFunctions.js').checkNewComment;
		this.interval = setInterval(function(){
			  checkNewComment(that.props.lastAccessTime, that)
			  }, 5000);
	}
	
	componentWillUnmount() {
		clearInterval(this.interval);
	}
	
	getAllComments = () =>{
		this.commentCount = 0;
		this.props.callbackFromParent();
	}
	
	render() {
		if(this.commentCount > 0){
			return (<div align="center" class="notification alert alert-info">
			<font color="red">You have <a href="#" onClick={this.getAllComments}>{this.commentCount}</a> new messages</font>
				</div>);
	      }
			return (<div class="notification">&nbsp;</div>)
		
     	
	}
		
}
	




