import React from 'react';
import './index.css';
import EditComment from './EditComment.js';

const divStyle = {
		   display: 'inline-block',
};
export default class AllCommentsBox extends React.Component {

	constructor(props) {
		super(props);
		this.comments = [];
		this.commentTexts = [];
	}

	
	getDate(date){
		return date.getDate() +"/"+ (date.getMonth() +1)+ "/"+ date.getFullYear() +
		"  " + date.getHours() +":"+ date.getMinutes();
	}
	

		handleChange = (commentArray) =>{
		this.commentTexts = []
		if(commentArray[0] !== undefined){
		this.comments = Array.from(commentArray);
		var that = this;
		/*this.comments.map(function(comment){
			that.commentTexts.push(comment.text);
		});*/
		}
		}

		saveComment = (edittedComment,edittedCommentId) =>{
			this.props.editCommentCallBack(edittedComment, edittedCommentId);
		}

		next = (e) =>{
			this.props.fetchTagComments(e.target.innerText);
		}
		
	    render() {
				this.handleChange(this.props.listNameFromParent);
				var that = this;
		return (
		  <div>
		  <div align="right">
		  	<a href="#" onClick={that.next.bind(this)}><span class="glyphicon glyphicon-step-backward"></span></a>&nbsp;&nbsp;
		  	<a href="#" onClick={that.next.bind(this)}><span class="glyphicon glyphicon-fast-backward"></span></a>&nbsp;&nbsp;
		  	<a href="#" onClick={that.next.bind(this)}><span class="glyphicon glyphicon-fast-forward"></span></a>&nbsp;&nbsp;
		  	<a href="#" onClick={that.next.bind(this)}><span class="glyphicon glyphicon-step-forward"></span></a>
		  </div>
				<div>{this.comments.map(function(comment){
					var date = new Date(comment.timestamp);
					return <div class="well">
					  <div>
					    <div>{comment.text}</div>
					    <div>&nbsp;</div>
					    <div class="right">
					    	<div style={divStyle}>By: {comment.user}&nbsp; &nbsp;at&nbsp; {that.getDate(date)}</div>
					    	{(require('./CommentFunctions.js').getLoggedInUser() === comment.user) &&
								<div  style={divStyle}>&nbsp;<EditComment saveCommentCallBack={that.saveComment} comment={comment}/></div>
							}
					</div>
					</div>
					</div>
				})}</div>
					
				
			</div>
	    );
	  }
	
	}


